// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'normalize.css';

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})


$(function() {
  //scroll viewer
  var revealClass = '.animated';
  var targetClass = '.has-scroll-reveal:not(' + revealClass + ')';
  var offset = 50; // Offset from bottom of viewport in pixels.
  var winHeight = $(window).height();
  // Recalc height of window in case of resize
  $(window).on('resizeEnd', function() {
    winHeight = $(window).height();
  });
  // Fire when page loads
  triggerAnimation(revealClass, targetClass, offset, winHeight);
  // Also fire on scroll
  $(window).on('scroll', function() {
    triggerAnimation(revealClass, targetClass, offset, winHeight);

    popPop();

  }); // window.on('scroll')
  function triggerAnimation(revealClass, targetClass, offset, winHeight) {
    // Get current scrollPos
    var trigger = $(window).scrollTop() + winHeight - offset;
    // Loop through elements we're affecting
    $(targetClass).each(function() {
      var elementOffset = $(this).offset().top;
      if( elementOffset < trigger ) {
        $(this).addClass( revealClass.replace('.','') );
      }
    });
  }
  $(window).resize(function() {
    if(this.resizeTO) clearTimeout(this.resizeTO);
    this.resizeTO = setTimeout(function() {
        $(this).trigger('resizeEnd');
    }, 500);
  });

  function popPop(){
    $('.item1.animated').each(function(i){
      setTimeout(function(){
        $('.item1.animated').eq(i).addClass('is-visible');
      }, 200 * i);
    });
    $('.item2.animated').each(function(i){
      setTimeout(function(){
        $('.item2.animated').eq(i).addClass('is-visible');
      }, 200 * i);
    });
    $('.item3.animated').each(function(i){
      setTimeout(function(){
        $('.item3.animated').eq(i).addClass('is-visible');
      }, 200 * i);
    });
    $('.item4.animated').each(function(i){
      setTimeout(function(){
        $('.item4.animated').eq(i).addClass('is-visible');
      }, 200 * i);
    });
    $('.item5.animated').each(function(i){
      setTimeout(function(){
        $('.item5.animated').eq(i).addClass('is-visible');
      }, 200 * i);
    });
  }

  popPop();
});



