import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Contacto from '@/components/Contacto'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/contacto',
      name: 'Contacto',
      component: Contacto
    }
  ]
})



